.Tweet {
  position: relative;
  width: 100%;
}

.user-tweet {
  width: 100%;
  margin-bottom: 0.5rem;
  border-bottom: 1px solid var(--border-color);
}

.tweet-content {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 0.5rem;
}

.Tweet-userImg {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid var(--point-color);
  margin-right: 5px;
}

.Tweet-userName {
  font-size: 0.9rem;
  margin-right: 0.5rem;
}

.tweet-date {
  color: var(--unselected-color);
  font-size: 0.7rem;
  font-weight: 400;
  letter-spacing: -0.05em;
}

.btn--heart__box {
  text-align: right;
}

.btn--heart {
  padding: 0.3rem 0.5rem;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 3px;
  outline: none;
  margin-bottom: 0.5rem;
}

.heart__icon {
  color: #db4437;
  margin-right: 5px;
}

.heart__count {
  font-size: 0.8rem;
  color: var(--unseleted-color);
}

.tweet-text {
  padding: 0.5rem;
  font-size: 0.9rem;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: var(--text-color);
  margin-bottom: 0.5rem;
}

.selected-img-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
}

.selected-img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.btn-box {
  position: absolute;
  display: flex;
  top: 5px;
  right: -5px;
}

.btn-edit,
.btn-delete {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 30px;
  height: 30px;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.btn-icon {
  color: var(--placeholder-color);
}

.btn-icon:hover {
  color: var(--primary-color);
}

.visually-hidden {
  opacity: 0;
  display: absoulte;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.edit-box {
  display: flex;
  justify-content: center;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid var(--border-color);
  margin-bottom: 1rem;
}

.edit-textarea {
  width: 17.5rem;
  height: 5rem;
  padding: 0.5rem;
  caret-color: var(--primary-color);
  outline: none;
  border: 1px dashed var(--border-color);
  background-color: #fff;
  border-radius: 3px;
  resize: none;
  margin-right: 5px;
}

.edit-textarea::placeholder {
  color: #d2d2d2;
  font-size: 1rem;
}

.edit-textarea::-webkit-scrollbar {
  width: 10px;
}

.edit-textarea::-webkit-scrollbar-thumb {
  background-color: var(--point-color);
  border-radius: 10px;
  background-clip: padding-box;
  border: 2px solid transparent;
}

.edit-textarea::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 10px;
}

.btn-edit-box {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.btn-control.btn-okay,
.btn-control.btn-cancel {
  padding: 0.4rem 0.9rem;
  background-color: var(--placeholder-color);
}

.btn-control.btn-okay:hover,
.btn-control.btn-cancel:hover {
  background-color: var(--primary-color);
}
